import React, { Fragment, useState } from 'react';
import { Link } from 'react-router-dom';
import imageLoader from '../../assets/images/image_loader.gif'

const Card = ({
  thumbnail,
  file,
  title,
  duration,
  artiste,
  genre,
  series,
  showPlayer,
  handleShowPlayer,
  setFile,
  setThumbnail,
  carouselClass,
  price,
  symbol,
  market,
}) => {
  const [loading, setLoading] = useState(false)
  const handleNowPlaying = () => {
    setFile(file);
    setThumbnail(thumbnail);
    handleShowPlayer(true);
  };
  return (
    <Fragment>
      <div
        className={`music__card__wrapper ${carouselClass ? carouselClass : ''}`}
      >
        {/* <Link to={`/${series}`}> */}
        <div className='music__card__title'>
          <Link
            to={
              market === true ? `/market/${series}` : `/collectible/${series}`
            }
          >
            <h4 className='text-center'>{title}</h4>
          </Link>
        </div>
        <div
          className='music__card__thumbnail'
          style={{ position: 'relative' }}
        >
          {loading ? (
            <img className={`card__image ${!loading && 'd-block'}`}
              src={thumbnail}
              alt='thumbnail'
              width={100}
              onLoad={() => setLoading(true)}
            />
          ) : (
            <img
              src={imageLoader}
              alt='thumbnail'
              width={100}
              onLoad={() => setLoading(true)}
            />
          )}
        </div>
        {price && (
          <div className='price__wrapper'>
            <h6>
              {price} {symbol}
            </h6>
          </div>
        )}
        <div className='play__button'>
          <button onClick={() => handleNowPlaying()}>Play now</button>
          <Link
            style={{
              color: 'black',
              fontSize: '11px',
              marginLeft: '4px',
              fontWeight: 'bold',
            }}
            to={
              market === true ? `/market/${series}` : `/collectible/${series}`
            }
          >
            view
          </Link>
        </div>
        <div className='music__card__footer'>
          <div className='music__card__artiste'>
            <small>Artiste:</small>
            <h6>{artiste}</h6>
          </div>
          <div className='music__card__extras'>
            <h6>
              <i className='fa fa-clock'></i>
              {duration}
            </h6>
            <h6>
              <span>Genre: </span>
              {genre}
            </h6>
          </div>
        </div>
        {/* </Link> */}
      </div>
    </Fragment>
  );
};

export default Card;
