/* eslint-disable no-mixed-operators */
import React, { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import Layout from '../../components/Layout';
import AudioPlayer from 'react-h5-audio-player';
import VideoPlayer from 'react-video-markers';
import Slider from 'react-slick';
import {
  fetchInterests,
  fetchOnMarket,
  getCollectible,
  getCollectibles,
} from '../../actions/collectibles';
import { addToCart, removeFromCart } from '../../actions/cart';
import { setUsdPrice } from '../../actions/market';
import Card from '../../components/cards/Card';
import {
  sellToken,
  buyToken,
  changePrice,
  cancelSell,
} from '../../actions/token';
import {
  getNFTInstances,
  getNFTInstance,
  getNFTDefinition,
  getNFTSellBook,
} from '../../actions/nfts';
import UploadLoader from '../../components/UploadLoader';
import { Link } from 'react-router-dom';
import ManageModal from '../../components/modals/ManageModal';
import SetPriceModal from '../../components/modals/SetPriceModal';
import CartModal from '../../components/modals/CartModal';
import { toFixedWithoutRounding } from '../../utils/helpers';
import MetaDecorator from '../../components/utils/MetaDecorator'

let mounted = false;
const MarketCollectible = ({
  match,
  getCollectible,
  getCollectibles,
  getNFTDefinition,
  getNFTInstances,
  getNFTInstance,
  getNFTSellBook,
  fetchInterests,
  fetchOnMarket,
  sellToken,
  buyToken,
  changePrice,
  cancelSell,
  setUsdPrice,
  addToCart,
  removeFromCart,
  usd_price,
  collectibles: { collectible, collectibles },
  username, authenticated,
  cart,
  nfts: { instances, sellbook, loading },
}) => {
  const [isShowPrice, setIsShowPrice] = useState(false);
  const [isShowManage, setIsShowManage] = useState(false);
  // const [isShowCart, setIsShowCart] = useState(false);
  const [price, setPrice] = useState('');
  const [instanceToSell, setInstanceToSell] = useState({});
  const [isPlaying, setIsPlaying] = useState(false);
  const [volume, setVolume] = useState(0.7);
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
    autoplay: true
  };
  const handleProgress = (e) => {
    // console.log('Current time: ', e.target.currentTime);
  };

  const handleDuration = (duration) => {
    console.log('Duration: ', duration);
  };
  const handlePlay = () => {
    setIsPlaying(true);
  };
  const handlePause = () => {
    setIsPlaying(false);
  };
  const handleVolume = (value) => {
    setVolume(value);
  };
  const {
    params: { series },
  } = match;

  const authorEnd = series.indexOf('_');
  const author = series.substring(0, authorEnd);
  useEffect(() => {
    mounted = true;
    getCollectible(series);
    getCollectibles(1);
    // getNFTDefinition();
    getNFTInstances(
      {
        // account: author,
      },
      series
    );
    getNFTSellBook(
      {
        
      },
      series
    );
    // fetchInterests();
    // fetchOnMarket({
    //   account: author,
    // });
    setUsdPrice();
    return () => (mounted = false);
  }, [
    series,
    username,
    getCollectible,
    getCollectibles,
    getNFTDefinition,
    getNFTInstances,
    getNFTInstance,
    getNFTSellBook,
    fetchInterests,
    fetchOnMarket,
    author,
    setUsdPrice,
  ]);

  return (
    <Fragment>
      <MetaDecorator
        description={collectible?.description}
        title={collectible?.name}
        imageUrl={collectible?.thumbnail}
        imageAlt={collectible?.metaImageAlt}
      />
      <Layout>
        {sellbook.some(
          (d) => d.account === username && d.series === series
        ) && (
          <div className='row'>
            <div className='col-md-12'>
              <div className='panel bg-black bg-light panel-success p-2'>
                <h6 className='text-center mt-3'>
                  <i className='fa fa-info-circle'></i> Kindly modify this
                  collection and manage it in{' '}
                  <Link to={`/collectible/${collectible?.series}`}>
                    <strong>Manage Collection</strong>
                  </Link>
                </h6>
              </div>
            </div>
          </div>
        )}
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <div className='collectible__title mt-3'>
                <h2 className='text-center'>{collectible?.name}</h2>
              </div>
            </div>
          </div>

          <div className='row'>
            <div className='col-md-1'></div>
            <div className='col-md-10'>
              <div
                className={`file__player mt-4 ${
                  (collectible?.type === 'audio' && 'w-75') ||
                  (collectible?.type === 'fan' && 'w-75')
                }`}
              >
                {collectible?.type === 'audio' ? (
                  <div style={{ width: '100%' }}>
                    <div className='audio__thumbnail'>
                      <img
                        src={collectible?.thumbnail}
                        alt='thumbnail'
                        width='100%'
                        height='350px'
                        style={{ objectFit: 'cover' }}
                      />
                    </div>
                    <AudioPlayer
                      autoPlay
                      src={collectible?.file}
                      onPlay={(e) => console.log('onPlay')}
                      // other props here
                    />
                  </div>
                ) : collectible?.type === 'fan' ? (
                  <div style={{ width: '100%' }}>
                    <div className='audio__thumbnail'>
                      <img
                        src={collectible?.thumbnail}
                        alt='thumbnail'
                        width='100%'
                        height='450px'
                        style={{ objectFit: 'cover' }}
                      />
                    </div>
                  </div>
                ) : (
                  mounted && (
                    <VideoPlayer
                      url={collectible?.file}
                      isPlaying={isPlaying}
                      volume={volume}
                      onPlay={handlePlay}
                      onPause={handlePause}
                      onVolume={handleVolume}
                      onProgress={handleProgress}
                      onDuration={handleDuration}
                    />
                  )
                )}
              </div>

              <div className='collectible__description mt-3'>
                <p className='text-center'>
                  <strong>Description:</strong> {collectible?.description}
                </p>
                <p className='text-center'>
                  <strong>Notes:</strong> {collectible?.notes}
                </p>
                <p className='text-center'>
                  <strong>Type:</strong> {collectible?.type}
                </p>
                <p className='text-center'>
                  <strong>Tags:</strong>{' '}
                  {collectible?.tags?.map((tag, index) => (
                    <span key={index}>#{tag} </span>
                  ))}
                </p>
                <p className='text-center'>
                  <strong>Featured:</strong>{' '}
                  {collectible?.featured === false ? 'false' : 'true'}
                </p>
                <p className='text-center'>
                  <strong>NSFW:</strong>{' '}
                  {collectible?.nsfw === false ? 'false' : 'true'}
                </p>
                <p className='text-center'>
                  <strong>Published:</strong>{' '}
                  {collectible?.published === false ? 'false' : 'true'}
                </p>
                <p className='text-center'>
                  <strong>Creator:</strong> {collectible?.creator}
                </p>
              </div>
              {
                authenticated ? (
              
              sellbook.length > 0 ? (
                <Fragment>
                  <div className='collectible__action__buttons text-center my-3'>
                    <table className='table table-bordered'>
                      <thead>
                        <tr>
                          <th scope='col'>ID</th>
                          <th scope='col'>Series</th>
                          <th scope='col'>Owner</th>
                          <th scope='col'>Price</th>
                          <th scope='col'>Action</th>
                          {/* sellbook[0]?.account !== username */}
                          {sellbook.some(
                            (d) => d.account !== username && d.series === series
                          ) && <th scope='col'></th>}
                        </tr>
                      </thead>
                      <tbody>
                        {loading && sellbook ? (
                          <tr className='text-center'>
                            <td>
                              {' '}
                              <UploadLoader />
                            </td>
                          </tr>
                        ) : (
                          sellbook
                            .filter((sb) => sb.series === series)
                            ?.map((instance) => {
                              return (
                                <tr key={instance?.nft_id}>
                                  <th scope='row'>{instance?.nft_id}</th>
                                  <td>
                                    {instance?.series
                                      .replace(/_/g, ' ')
                                      .replace(/-/g, ' ')
                                      .toUpperCase()}
                                  </td>
                                  <td>{instance?.account}</td>
                                  <td>
                                    {instance.price}{' '}
                                    <small>{instance.symbol}</small> /
                                    <b>
                                      $
                                      {toFixedWithoutRounding(
                                        instance.price * usd_price
                                      )}
                                    </b>
                                  </td>
                                  <td>
                                    {instance.account === username ? (
                                      <div className='flex'>
                                        <button
                                          className='btn btn-success btn-sm m-1'
                                          onClick={() => {
                                            setInstanceToSell(instance);
                                            setIsShowPrice(true);
                                          }}
                                        >
                                          <i className='fa fa-cogs'></i>
                                        </button>
                                        <button
                                          type='button'
                                          className='btn btn-info btn-sm m-1'
                                          onClick={() => {
                                            setInstanceToSell(instance);
                                            setIsShowManage(true);
                                          }}
                                        >
                                          <i className='fa fa-pencil-alt'></i>
                                        </button>
                                        <button
                                          type='button'
                                          className='btn btn-danger btn-sm m-1'
                                          onClick={() => {
                                            cancelSell(instance.nft_id);
                                          }}
                                        >
                                          <i className='fa fa-times'></i>
                                        </button>
                                      </div>
                                    ) : (
                                      <button
                                        onClick={() =>
                                          buyToken(
                                            instance.price,
                                            instance.nft_id.toString()
                                          )
                                        }
                                      >
                                        Buy
                                      </button>
                                    )}
                                  </td>
                                  {sellbook.some(
                                    (d) =>
                                      d.account !== username &&
                                      d.series === series
                                  ) && (
                                    <td>
                                      {cart.find(
                                        (item) =>
                                          item.nft_id === instance.nft_id
                                      ) === undefined ? (
                                        <button
                                          onClick={() =>
                                            addToCart({
                                              ...instance,
                                              thumbnail: collectible.thumbnail,
                                              name: collectible.name,
                                              description:
                                                collectible.description,
                                            })
                                          }
                                        >
                                          <i
                                            className='fa fa-shopping-cart'
                                            aria-hidden='true'
                                          ></i>
                                          <span>add</span>
                                        </button>
                                      ) : (
                                        <button
                                          className={
                                            cart.find(
                                              (item) =>
                                                item.nft_id === instance.nft_id
                                            ) !== undefined
                                              ? `btn-danger`
                                              : ''
                                          }
                                          onClick={() =>
                                            removeFromCart(instance.nft_id)
                                          }
                                        >
                                          <i
                                            className='fa fa-shopping-cart'
                                            aria-hidden='true'
                                          ></i>{' '}
                                          <span>remove</span>
                                        </button>
                                      )}
                                    </td>
                                  )}
                                </tr>
                              );
                            })
                        )}
                      </tbody>
                    </table>
                    <ul></ul>
                  </div>
                </Fragment>
              ) : (
                <Fragment>
                  <h4 className='text-center mt-3'>
                    No music here yet... Get creative and make some!
                  </h4>
                </Fragment>
              )) : (
                <div className='not__auth__button__wrapper text-center my-5'>
                  <button>Login</button>
                  <h4 className='mt-3'>You must login to buy or sell an NFT</h4>
                </div>
              )}
            </div>
          </div>
          <hr />
          <div className='row'>
            <Slider {...settings}>
              {collectibles.map((collec, index) => (
                <Card
                  key={index}
                  title={collec?.collection_name}
                  thumbnail={collec?.thumbnail}
                  file={collec?.file}
                  artiste={collec?.creator}
                  genre={collec?.category}
                  duration={collec?.type}
                  series={collec?.series}
                  carouselClass='mx-1'
                />
              ))}
            </Slider>
          </div>
        </div>
        <ManageModal
          isShow={isShowManage}
          setIsShow={setIsShowManage}
          changePrice={changePrice}
          instance={instanceToSell}
        />
        <SetPriceModal
          isShow={isShowPrice}
          setIsShow={setIsShowPrice}
          setPrice={setPrice}
          price={price}
          instance={instanceToSell}
          sellToken={sellToken}
        />
        <CartModal />
      </Layout>
    </Fragment>
  );
};
const mapStateToProps = (state) => ({
  collectibles: state.collectibles,
  nfts: state.nfts,
  username: state.users.username,
  authenticated: state.users.authenticated,
  cart: state.cart.cart,
  usd_price: state.market.usd_price,
});
export default connect(mapStateToProps, {
  getCollectible,
  getCollectibles,
  sellToken,
  buyToken,
  changePrice,
  cancelSell,
  addToCart,
  removeFromCart,
  getNFTDefinition,
  getNFTInstances,
  getNFTInstance,
  getNFTSellBook,
  fetchInterests,
  fetchOnMarket,
  setUsdPrice,
})(MarketCollectible);
