import {
  ADD_TO_CART,
  REMOVE_FROM_CART,
  CLEAR_CART,
  ADD_SELL_CART,
  REMOVE_SELL_CART,
  CLEAR_SELL_CART,
} from '../actions/types';

const intialState = {
  cart: [],
  sell_cart: [],
  total: 0,
};

export const cartReducer = (state = intialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case ADD_TO_CART:
      return {
        ...state,
        cart: [...state.cart, payload],
        total: state.total + payload.price,
      };

    case REMOVE_FROM_CART:
      const item = state.cart.find((i) => i.nft_id === payload);
      return {
        ...state,
        cart: state.cart.filter((item) => item.nft_id !== payload),
        total: state.total - item.price,
      };
    case ADD_SELL_CART:
      return {
        ...state,
        sell_cart: [...state.sell_cart, ...payload],
      };
    case REMOVE_SELL_CART:
      return {
        ...state,
        sell_cart: state.sell_cart.filter((s) => s !== payload),
      };
    case CLEAR_SELL_CART:
      return {
        ...state,
        sell_cart: [],
      };
    case CLEAR_CART:
      return [];
    default:
      return state;
  }
};

export default cartReducer;
