import React, { Fragment } from 'react';

const Tag = ({ icon, title, handleSearch }) => {
  return (
    <Fragment>
      <div className='tag__wrapper'>
        <button
          className='bg-transparent'
          onClick={() => handleSearch(title === 'All' ? null : title)}
        >
          <img src={icon} alt={title} width={30} />
          <strong>{title}</strong>
        </button>
      </div>
    </Fragment>
  );
};

export default Tag;
