import React from 'react'

const SearchField = (handleSubmit, setSearch, search) => {
    return (
      <div>
        <div className='row d-none'>
          <div className='col-md-12'>
            <div className='section__title text-center mb-0'>
              <form className='d-flex' onSubmit={handleSubmit}>
                <div className='nfttunz__search__wrapper'>
                  <input
                    value={search}
                    onChange={(e) => {
                      setSearch(e.target.value);
                    }}
                    className='nfttunz__navbar__search__input w-100'
                    type='search'
                    placeholder='Search'
                    aria-label='Search'
                  />
                  <button className='btn nfttunz__icon' type='submit'>
                    {' '}
                    <i className='fa fa-search'></i>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
}

export default SearchField
